import React from 'react';
import PropTypes from 'prop-types';
import { navigateTo } from 'gatsby-link';
import { withStyles } from 'material-ui/styles';

import Chip from 'material-ui/Chip';

const propTypes = {
  tags: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array,
  ]),
};

const defaultProps = {
  tags: '',
};

const styles = theme => ({
  root: {
    margin: 10,
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
  },
  chip: {
    marginRight: 10,
    marginTop: 10,
  },
});

const Tags = (props) => {
  const { classes, tags } = props;

  if (!tags) {
    return null;
  }

  let tagArr = tags;
  if (typeof tags === 'string') {
    tagArr = tags.split(',');
  }

  return (
    <div className={classes.root}>
      {tagArr && tagArr.sort().map(tag => (
        <Chip
          className={classes.chip}
          key={tag}
          label={tag}
          onClick={() => navigateTo(`/?p=${tag}`)}
        />
      ))}
    </div>
  );
};

Tags.propTypees = propTypes;
Tags.defaultProps = defaultProps;

export default withStyles(styles, { name: 'StyledTags' })(Tags);



